import { Component, OnInit, Input } from '@angular/core';
import { Car } from '../model/car';
import { DataServiceService } from '../data-service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-car-item',
  templateUrl: './car-item.component.html',
  styleUrls: ['./car-item.component.css']
})
export class CarItemComponent implements OnInit {
  public interestFlag:boolean;
  @Input() cardetail:Car;
  constructor(private ds:DataServiceService,private route:Router) { }
  
  ngOnInit() {
    this.interestFlag=false;
  }
  public viewCar(){
    this.ds.selectedCar=this.cardetail;
    this.route.navigate(["car-detail"]);
  } 
  sendInterest(){
    this.ds.selectedCar=this.cardetail;
    this.route.navigate(["send-interest"]);
  }
}
