import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CarsComponent } from './cars/cars.component';
import { SellCarComponent } from './sell-car/sell-car.component';
import { CarDetailsComponent } from './car-details/car-details.component';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { InterestDetailsComponent } from './interest-details/interest-details.component';

const routes: Routes = [
  {path:"send-interest",component:InterestDetailsComponent},
  {path:"signup",component:SignupComponent},
  {path:"login",component:LoginComponent},
  {path:"sell-car",component:SellCarComponent},
  {path:"car-detail",component:CarDetailsComponent},
  {path:"**",component:CarsComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
