import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DataServiceService } from '../data-service.service';
import { carModel } from '../model/carModel';

@Component({
  selector: 'app-sell-car',
  templateUrl: './sell-car.component.html',
  styleUrls: ['./sell-car.component.css']
})
export class SellCarComponent implements OnInit {
  @ViewChild('imageurl') fileInput: any;
  constructor(private http: HttpClient, private ds: DataServiceService) {
    this.cardata = { id: 0, name: "", modelID: 0, makeID: 0, city: "", fuelTypeID: 0, kilometersDriven: 0, price: 0, makeYear: "", ownerTypeID: 0, registrationNumber: "", insuranceTypeID: 0, registrationTypeID: 0 };
  }
  public cardata: carModel;

  public makeurl = "https://carwikaapi.azurewebsites.net/api/ReValuedCars/Makes";
  public modelurl = "https://carwikaapi.azurewebsites.net/api/ReValuedCars/Models/";
  public makeList;
  public modelList;
  public cityList;
  public RegYears = ["2000", "2001", "2002", "2003", "2004", "2005", "2006", "2007", "2008", "2009", "2010", "2011", "2012", "2013", "2014", "2015", "2016", "2017", "2018", "2019"];
  public ownerList;
  ngOnInit() {
    this.http.get(this.makeurl).subscribe(res => {
      this.makeList = res;
    });
    this.http.get(this.modelurl + 1).subscribe(res => {
      this.modelList = res;
    });
    this.ds.getCities().subscribe(res => { this.cityList = res; });
    this.ds.getOwnerType().subscribe(res => { this.ownerList = res; });
  }
  changeOnMake(make) {
    this.http.get(this.modelurl + make.value).subscribe(res => {
      this.modelList = res;
    });


  }  
  sellcar(selldata) {
    console.log(this.cardata);
    let Savedcar;
    this.http.post("https://carwikaapi.azurewebsites.net/api/ReValuedCars/Cars",this.cardata).subscribe(res=>{Savedcar=res;});
    let nativeElement: HTMLInputElement = this.fileInput.nativeElement;
    setTimeout(() => {
      this.upload(nativeElement.files,Savedcar.id);
    }, 3000);
    
    console.log(this.fileInput);
  }
  upload(file: any,carId:any) {
    var input = new FormData();
    input.append("filesData", file[0]);
    console.log(input);
    return this.http.post("https://carwikaapi.azurewebsites.net/api/ReValuedCars/CarImage?carId="+carId, input).subscribe(res => { console.log(res) });
  }

}
